.wrapper {
    width: 500px;
    margin: 10px auto;
    font-size: 14px;
    background-color: #fff;
    border: 1px solid #f1f1f1;
    padding: 20px;
}

.papa {
    overflow: auto;
    border-bottom: dotted #ddd .5px;
}
.lr {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: start;
        align-items: flex-start;
    padding: 1vh 1vw;
}
.lr span{
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-preferred-size: 1vw;
        flex-basis: 1vw;
}
.lr > input,
.lr > textarea {
    border-radius: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 3 1;
        flex: 3 1;
}
.btn {
    position: relative;
    float: right;
}
.btn>input {
    background-color: #39CCCC;
    text-align: center;
}
.commentDiv {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    border-bottom: solid .5px #ddd;
}
.commentName span {
    color: #39CCCC ;
}
.commentName {
    margin:  2vh 3vw;
}



